<template>

	<div class="index-shuffer" id="six">
		<div class="bw">
			<div class="shuffer-title">
				<img src="./../img/wrap/6/shuffer-title.web.png" class="thumb-img-lazy" />
			</div>
			<div class="stars">
				<div class="stars-wall">
					<div class="wall-layer wall-layer-left">
					</div>
					<div class="bx-wrapper" style="max-width: 1715px;">
						<div class="bx-viewport" style="width: 100%; overflow: hidden; position: relative; height: 240px;">
							<ul class="stars-lists clearfix" style="width:20215%;position: relative;transition:all 0.4s ease-in;-webkit-transition:all 0.4s ease-in;-ms-transition:all 0.4s ease-in;" :style="{transform:'translateX(' +lef+ 'px' + ')'}">
								<pp-li v-for="(item,index) in list" :key='index' :item='item'></pp-li>
							</ul>
						</div>
					</div>
					<div class="wall-layer wall-layer-right">
					</div>
				</div>
				<div class="arrows">
					<div class="arrow arrow-left" @click="pre">
						<a href="javascript:void(0);" class="bx-prev disabled"></a>
					</div>
					<div class="arrow arrow-right" @click="nex">
						<a href="javascript:void(0);" class="bx-next"></a>
					</div>
				</div>
			</div>
		</div>
	</div>

</template>

<script>
	export default {
		data: function() {
			return {
				list: [{
						id: 1,
						src: '../static/img/wrap/6/0.web.png',
						title: '剧本杀青衣怪谈biubiu',
						name: '饭岛ash'
					},
					{
						id: 2,
						src: '../static/img/wrap/6/1.web.png',
						title: '微博:周cici酱，qq群已满',
						name: '周cici酱'
					},
					{
						id: 3,
						src: '../static/img/wrap/6/2.web.png',
						title: '微博：mua车车酱',
						name: 'mua车车酱'
					},
					{
						id: 4,
						src: '../static/img/wrap/6/3.web.png',
						title: '感谢曾一起走过的青春岁月！',
						name: 'PD丶叫兽'
					},
					{
						id: 5,
						src: '../static/img/wrap/6/4.web.png',
						title: '关注微博：GRACEKKKKKK',
						name: '大姨妈阿'
					},
					{
						id: 6,
						src: '../static/img/wrap/6/5.web.png',
						title: '关注微博：皮阿卡9丶，感恩遇见。',
						name: '皮阿卡9丶'
					},
					{
						id: 7,
						src: '../static/img/wrap/6/6.web.png',
						title: 'IG牛逼！',
						name: '李瑞轩'
					},
					{
						id: 8,
						src: '../static/img/wrap/6/7.web.png',
						title: 'heeeey',
						name: 'paipaipaipaipai'
					},
					{
						id: 9,
						src: '../static/img/wrap/6/8.web.png',
						title: '休息，有事私信',
						name: '小能同志'
					},
					{
						id: 10,
						src: '../static/img/wrap/6/9.web.png',
						title: '微博关注：沙拉小宝贝',
						name: '沙拉sala'
					},
					{
						id: 11,
						src: '../static/img/wrap/6/10.web.png',
						title: '微博关注：细软绫小萌',
						name: '细软绫小萌'
					},
					{
						id: 12,
						src: '../static/img/wrap/6/11.web.png',
						title: '微博尢娜酱 感恩遇见',
						name: '尢娜'
					},
				],
				current: 1,
				width: -980,
				settime: false,
			}
		},
		computed: {
			lef: function() {
				return this.current * this.width;
			}
		},
		created: function() {
			var leng = this.list.length;
			var pr = [];
			var ne = []
			for(var i = leng; i > 0; i--) {
				if(i > leng - 4) {
					pr.unshift(this.list[i - 1])
				}
			}
			for(var i = 0; i < leng; i++) {
				if(i < 4) {
					ne.push(this.list[i])
				}
			}
			this.list = this.list.concat(ne);
			this.list = pr.concat(this.list);
		},
		methods: {
			pre: function() {
				if(!this.settime) {
					this.settime=true
					var lis = document.getElementById("six").getElementsByClassName("stars-lists")[0]
					var _this = this
					setTimeout(function(){
						_this.settime=false
					},600)
					this.current -= 1;
					if(this.current == 0) {
						setTimeout(function() {
							lis.style.transitionDuration = '0s'
							_this.current = 3
						}, 500)
						setTimeout(function() {
							lis.style.transitionDuration = '0.4s'
						}, 600)
					}

				}
			},
			nex: function() {
				if(!this.settime) {
					this.settime=true
					var lis = document.getElementById("six").getElementsByClassName("stars-lists")[0]
					var _this = this
					setTimeout(function(){
						_this.settime=false
					},600)
					this.current += 1;
					if(this.current == 4) {
						setTimeout(function() {
							lis.style.transitionDuration = '0s'
							_this.current = 1
						}, 500)
						setTimeout(function() {
							lis.style.transitionDuration = '0.4s'
						}, 600)
					}

				}
			}
		},
		components: {
			'pp-li': function() {
				return import('./pp-li.vue')
			}
		}
	}
</script>

<style>

</style>


